.search {
  margin: 1rem 0;
  padding: 0.5rem;
  width: 100%;
  background-color: aquamarine;
  border: 0;
  font-size: 1.5rem;
  box-shadow: 0rem 0rem 1rem 0.5rem rgba(127, 255, 212, 0.4);
  border-radius: 0.5rem;
}

.search:hover,
.search:focus {
  box-shadow: 0rem 0rem 1rem 0.7rem rgba(127, 255, 212, 0.4);
}